import React, { Component } from 'react';
import { PieChart, Pie, Tooltip, Cell, Legend, ResponsiveContainer } from 'recharts';

class Event extends Component {
  state = {
    showDetails: false
  }

  handleShowDetails = () => {
    if(this.state.showDetails === false) {
      this.setState({ showDetails: true });
    }
    else {
      this.setState({ showDetails: false });
    }
  }

  render() {
    const showDetails = this.state.showDetails;
    const event = this.props.event;
    const data=[{ name: "people coming", value: event.yes_rsvp_count }, { name: "slots left", value: (event.rsvp_limit - event.yes_rsvp_count) }];
    const colors =[ "#8884d8", "#37c0ba"];

    return(
      <div className="event">
        <div className="event__Overview">
          <h2 className="event__Overview--name">{this.props.event.name}</h2>
          <p className="event__Overview--localDate">When: {this.props.event.local_date}, {this.props.event.local_time}</p>
          <ResponsiveContainer height={100} width={230}>
            <PieChart>
            <Pie data= {data} dataKey="value" nameKey="name" cx="50%" cy="50%" outerRadius={32}  label >
            {
              data.map((entry, index) => (<Cell key={`cell-${index}`} fill={colors[index]}/>))
            }
            </Pie>
            <Legend iconSize={10} iconType = "triangle" layout="horizontal" verticalAlign="bottom" align="center" />
            <Tooltip/>
            </PieChart>
          </ResponsiveContainer>
          {showDetails &&
            <button className="details-btn" onClick={() => this.handleShowDetails()}>hide details</button>
          }
          {!showDetails &&
            <button className="details-btn" onClick={() => this.handleShowDetails()}>show details</button>
          }
        </div>
        {showDetails &&
          <div className="event__Details">
            <h3>Infos</h3>
            <p className="event__Details--description" dangerouslySetInnerHTML={{ __html: this.props.event.description}} />
          </div>
        }
      </div>
    );
  }
}

export default Event;
